@import url(https://fonts.googleapis.com/css?family=Finger+Paint);
.banner{font-size:30px}.nav-bar{display:flex;list-style:none;padding:20px;justify-content:flex-end;background-color:#59DC73}.nav-link{padding:20px}

.homepage-container{height:500px}.main-container{display:flex;height:500px;background-color:cyan;justify-content:center;align-items:center}.main-container .main-container-heading{text-align:center}.main-container .main-container-welcome{-webkit-animation:fadein 20s infinite;animation:fadein 20s infinite;margin:0}@-webkit-keyframes fadein{0%{opacity:0}20%{opacity:1}80%{opacity:1}100%{opacity:0}}@keyframes fadein{0%{opacity:0}20%{opacity:1}80%{opacity:1}100%{opacity:0}}.main-container .main-container-to{color:red;-webkit-animation:slidein 12s;animation:slidein 12s}@-webkit-keyframes slidein{0%{margin-left:-1000px}20%{margin-left:-1000px}35%{margin-left:0px}100%{margin-left:0px}}@keyframes slidein{0%{margin-left:-1000px}20%{margin-left:-1000px}35%{margin-left:0px}100%{margin-left:0px}}.main-container .main-container-wyndmoor{-webkit-animation:slideup 18s;animation:slideup 18s;color:purple;margin:0}@-webkit-keyframes slideup{0%{-webkit-transform:translateY(500px);transform:translateY(500px)}20%{-webkit-transform:translateY(500px);transform:translateY(500px)}35%{-webkit-transform:translateY(0px);transform:translateY(0px)}100%{-webkit-transform:translateY(0px);transform:translateY(0px)}}@keyframes slideup{0%{-webkit-transform:translateY(500px);transform:translateY(500px)}20%{-webkit-transform:translateY(500px);transform:translateY(500px)}35%{-webkit-transform:translateY(0px);transform:translateY(0px)}100%{-webkit-transform:translateY(0px);transform:translateY(0px)}}.image-container{display:inline-flex}.image-quote{padding:50px;height:200px;line-height:3;margin:0 auto}.nature-image{float:left;background-image:url(/react-wyndmoor/static/media/l_20130610-fairmount-park-1200.fea01ece.jpg);background-size:100% 100%;height:300px;color:white}.shops-image{float:left;background:url(/react-wyndmoor/static/media/chestnut-hill-philadelphia-1.9e39e9ca.jpg);background-size:100% 100%;height:300px;color:blue}.home-image{float:left;background:url(/react-wyndmoor/static/media/nicehouse.8b201138.jpg);height:300px;background-size:100% 100%;color:blue}.college-image{float:left;background:url(/react-wyndmoor/static/media/college-photo_22247.7bb9586d.jpg);height:300px;background-size:100% 100%;color:white}

.about-container{text-align:center;font:2vw/10vh "Finger Paint";margin-top:30px}h3{overflow:hidden;border-right:.15em solid orange;white-space:nowrap;margin:0 auto;letter-spacing:.15em;font-family:"Courier New";-webkit-animation:typing 7s steps(40, end),blink-caret 0.75s step-end infinite;animation:typing 7s steps(40, end),blink-caret 0.75s step-end infinite}@-webkit-keyframes typing{from{width:0}to{width:100%}}@keyframes typing{from{width:0}to{width:100%}}@-webkit-keyframes blink-caret{from,to{border-color:transparent}50%{border-color:orange}}@keyframes blink-caret{from,to{border-color:transparent}50%{border-color:orange}}

.button-container{height:500px;display:flex;align-items:center}.big-jelly-btn{cursor:pointer;display:block;justify-content:center;padding:2.5rem 7.5rem;border-radius:3.75rem;font-size:2rem;font-weight:600;border:1px solid #012880;background-image:-webkit-gradient(linear, left top, left bottom, from(#FF89D6), to(#C01F9E));background-image:linear-gradient(-180deg, #FF89D6 0%, #C01F9E 100%);box-shadow:0 1rem 1.25rem 0 rgba(22,75,195,0.5),0 -0.25rem 1.5rem #6e0f9b inset,0 0.75rem 0.5rem rgba(255,255,255,0.4) inset,0 0.25rem 0.5rem 0 #b446cf inset}.big-jelly-btn span{color:transparent;background-image:-webkit-gradient(linear, left bottom, left top, from(#EE82DA), to(#FEFAFD));background-image:linear-gradient(0deg, #EE82DA 0%, #FEFAFD 100%);-webkit-background-clip:text;background-clip:text;-webkit-filter:drop-shadow(0 2px 2px #506);filter:drop-shadow(0 2px 2px #506)}.big-jelly-btn::before{content:"";display:block;height:0.25rem;position:absolute;top:0.5rem;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);width:calc(100% - 7.5rem);background:#fff;border-radius:100%;opacity:0.7;background-image:-webkit-gradient(linear, left top, right top, from(rgba(255,255,255,0)), color-stop(20%, #fff), color-stop(80%, #fff), to(rgba(255,255,255,0)));background-image:linear-gradient(-270deg, rgba(255,255,255,0) 0%, #fff 20%, #fff 80%, rgba(255,255,255,0) 100%)}.big-jelly-btn::after{content:"";display:block;height:0.25rem;position:absolute;bottom:0.75rem;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);width:calc(100% - 7.5rem);background:#fff;border-radius:100%;-webkit-filter:blur(1px);filter:blur(1px);opacity:0.05;background-image:-webkit-gradient(linear, left top, right top, from(rgba(255,255,255,0)), color-stop(20%, #fff), color-stop(80%, #fff), to(rgba(255,255,255,0)));background-image:linear-gradient(-270deg, rgba(255,255,255,0) 0%, #fff 20%, #fff 80%, rgba(255,255,255,0) 100%)}.big-jelly-btn__really{-webkit-animation:spin 2s;animation:spin 2s}@-webkit-keyframes spin{from{-webkit-transform:rotate(90deg);transform:rotate(90deg)}to{-webkit-transform:rotate(2880deg);transform:rotate(2880deg)}}@keyframes spin{from{-webkit-transform:rotate(90deg);transform:rotate(90deg)}to{-webkit-transform:rotate(2880deg);transform:rotate(2880deg)}}.big-jelly-btn__try{-webkit-animation:glow 2s infinite;animation:glow 2s infinite;-webkit-transform:scale(1.5);transform:scale(1.5);opacity:0.25}@-webkit-keyframes glow{20%{background-color:pink;opacity:0.25}40%{background-color:pink;opacity:.5}60%{background-color:pink;opacity:1}80%{background-color:pink;opacity:.5}100%{background-color:pink;opacity:.25}}@keyframes glow{20%{background-color:pink;opacity:0.25}40%{background-color:pink;opacity:.5}60%{background-color:pink;opacity:1}80%{background-color:pink;opacity:.5}100%{background-color:pink;opacity:.25}}.contact-text{color:blue;-webkit-animation:contactemail 6s;animation:contactemail 6s;-webkit-transform:scale(2);transform:scale(2)}@-webkit-keyframes contactemail{from{-webkit-transform:scale(0);transform:scale(0)}to{-webkit-transform:scale(2);transform:scale(2)}}@keyframes contactemail{from{-webkit-transform:scale(0);transform:scale(0)}to{-webkit-transform:scale(2);transform:scale(2)}}

